import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

const childRoutes: Routes = [
  {
    path: "colecciones",
    children: [
      {
        path: "",
        loadChildren: () =>
          import("./colecciones/colecciones.module").then(
            (m) => m.ColeccionesModule
          ),
      },
    ],
  },
  {
    path: "principalunisofom",
    children: [
      {
        path: "",
        loadChildren: () =>
          import("./principalunisofom/principalunisofom.module").then(
            (m) => m.PrincipalunisofomModule
          ),
      },
    ],
  },
  { path: "", redirectTo: "/pages/principalunisofom" },
];

@NgModule({
  imports: [RouterModule.forChild(childRoutes)],
  exports: [RouterModule],
})
export class ChildRoutesModule {}
